h2 {
	color: blue;
}
.main {
	flex: 1 1 auto;
	position: relative;
	.main-title {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		h1 {
			font-family: "DM Serif Display";
			font-style: normal;
			font-weight: 400;
			font-size: 64px;
			line-height: 88px;
			color: #ffffff;
			text-align: center;
			margin-top: 247px;
		}
		.main-buttons {
			display: flex;
			justify-content: center;
			button {
				height: 56px;
				width: 180px;
				border-radius: 38px;
				font-family: "Poppins";
				font-style: normal;
				font-weight: 600;
				font-size: 16px;
				line-height: 24px;
				text-align: center;
				letter-spacing: 0.04em;
				text-transform: uppercase;
				border: none;
				margin-top: 24px;
				cursor: pointer;
			}
			.shop {
				background: #ff8f8f;
				color: #ffffff;
				margin-right: 16px;
				&:hover {
					background: #e86e6e;
				}
			}

			.about {
				background: #ffe4bb;
				color: #e86e6e;
				margin-left: 16px;
				&:hover {
					background: #ffdca7;
				}
			}
		}
	}
}
.slider-main {
	margin-top: 24px;
	position: relative;
	.slick-next {
		right: 32px;
	}
	.slick-next::before {
		content: url(../img/Chevron-right-large.png);
		color: white;
	}
	.slick-prev {
		left: 32px;
	}
	.slick-prev::before {
		content: url(../img/Chevron-left-large.png);
		color: white;
	}
	.slick-arrow {
		position: absolute;
		top: 50%;
		z-index: 2;
		font-size: 0;
	}
	.slider_element-main {
		position: relative;
		.main-img {
			width: 100%;
			max-width: 100%;
			height: auto;
		}
		.first-main-svg {
			position: absolute;
			top: 205px;
			left: 18%;
		}
		.second-main-svg {
			position: absolute;
			top: 177px;
			left: 75%;
		}
		.third-main-svg {
			position: absolute;
			top: 417px;
			left: 83%;
		}
	}
	.slick-dots {
		display: flex;
		justify-content: center;
		margin-top: -40px;
	}
	.slick-dots li {
		display: block;
		button {
			width: 15px;
			height: 15px;
			border-radius: 50%;
			border: none;
			background: #ffffff;
			opacity: 0.7;
			font-size: 0;
			margin-left: 4px;
			margin-right: 4px;
		}
	}
	.slick-dots li[class="slick-active"] button {
		background: #eac8be;
		opacity: 0.999;
	}
}


.quiet{
	width: 100%;
	background-color: #cee8ff;

	.quiet-books {
		position: relative;
		width: 100%;
		height: 1185px;
		max-width: 1440px;
		padding-left: 80px;
		padding-right: 80px;
		padding-top: 100px;
		margin: 0 auto;
		margin-top: 20px;
		.title-books {
			display: flex;
			justify-content: space-between;
			h2 {
				font-family: "DM Serif Display";
				font-style: normal;
				font-weight: 400;
				font-size: 64px;
				line-height: 88px;
				color: #105393;
				&::after {
					content: url(../img/h2_svg.svg);
					margin-left: -50px;
				}
			}
			p {
				font-family: "Poppins";
				font-style: normal;
				font-weight: 600;
				font-size: 32px;
				line-height: 48px;
				color: #105393;
			}
		}
		.circle-books {
			position: relative;
			.learning {
				position: absolute;
				top: 0;
				left: 0;
			}
			.easy_to_carry {
				position: absolute;
				top: 106px;
				left: 455px;
			}
			.sustainability {
				position: absolute;
				top: 51px;
				left: 916px;
			}
			.long-lasting_products {
				position: absolute;
				top: 398px;
				left: 150px;
			}
			.versatile_toys {
				position: absolute;
				top: 425px;
				left: 764px;
			}
			.out-box {
				width: 365px;
				height: 365px;
				border-radius: 50%;
				background-color: white;
				display: flex;
				align-items: center;
				z-index: 1;
				.in-box {
					font-family: "Poppins";
					font-style: normal;
					width: 349px;
					height: 349px;
					border-radius: 50%;
					background-color: white;
					border: 3px dashed #ff8f8f;
					margin: auto;
					display: flex;
					flex-direction: column;
					align-items: center;
					justify-content: center;
					text-align: center;
					h3 {
						font-weight: 600;
						font-size: 20px;
						line-height: 30px;
						text-align: center;
						color: #414249;
					}
					p {
						display: block;
						width: 253px;
						font-weight: 600;
						font-size: 16px;
						line-height: 24px;
						text-align: center;
						color: #414249;
					}
				}
			}
		}
		.bg_line {
			position: absolute;
			right: -13px;
			bottom: 0;
		}
	}
	}

