.header {
	font-family: "Poppins", sans-serif;
}
.content {
	width: 100%;
	background: #ffe4bb;
	box-sizing: border-box;
	box-shadow: (0px 4px 4px rgba(0, 0, 0, 0.25));
}
.content__slider {
	margin: auto;
	padding-top: 10px;
	height: 40px;
	width: 100%;
	max-width: 1440px;
}
.wrapper {
	max-height: 40px;
}
.slick-dots li {
	display: none;
}

.slider span {
	font-style: normal;
	font-weight: 400;
	font-size: 14px;
	line-height: 21px;
	color: #414249;
}

.slider__element {
	text-align: center;
}

.icons::before {
	font-size: 32px;
}
.slick-arrow {
	background-color: transparent;
	border: none;
	position: absolute;
	top: -8px;
	z-index: 99;
}
.slick-prev {
	left: 90px;
}
.slick-prev:before {
	content: "<";
	color: #414249;
	font-size: 30px;
}
.slick-next {
	right: 90px;
}
.slick-next:before {
	content: ">";
	color: #414249;
	font-size: 30px;
}

.icon-phone {
	font-size: 24px;
}
.phone {
	color: #ff8f8f;
}
.general {
	width: 100%;
	max-width: 1440px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: 24px auto;
	padding-left: 80px;
	padding-right: 80px;
}
.lang {
	margin-left: 75px;
	width: 35px;
	appearance: none;
	padding-left: 8px;
	padding-right: 8px;
	border: none;
	&:focus {
		border: 1px solid #e2e2e2;
	}
}

.logo {
	margin-left: 230px;
}
.admin {
	margin-left: 276px;
	display: flex;
	justify-content: space-between;
	width: 240px;
	font-size: 24px;
}
.icon-search,
.space {
	margin-left: -32px;
	&:hover {
		color: #ff8f8f;
	}
}
.space{
	margin-left: -52px;
}
.icon-heart-2{
	margin-left: -62px;
}

.navigation {
	max-width: 1440px;
	padding-left: 70px;
	padding-right: 70px;
	margin-top: -10px;
	margin: -10px auto -15px;
}
#menu {
	background: #ffffff;
	color: #fff;
	height: 45px;
}
#menu ul,
#menu li {
	padding: 0;
	list-style: none;
}
#menu ul {
	width: 100%;
	display: flex;
	justify-content: space-between;
}
#menu li {
	float: left;
	display: inline;
	position: relative;
	z-index: 1;
}
#menu a {
	display: block;
	line-height: 45px;
	padding: 0 14px;
	text-decoration: none;
	color: #414249;
	font-size: 14px;
	font-weight: 600;
}
#menu a.dropdown-arrow:after {
	content: "\23F7";
	margin-left: 5px;
	color: #ff8f8f;
}
#menu li a:hover {
	color: #ff8f8f;
}
#menu input {
	display: none;
	margin: 0;
	padding: 0;
	height: 45px;
	width: 100%;
	opacity: 0;
	cursor: pointer;
}
#menu label {
	display: none;
	line-height: 45px;
	text-align: center;
	position: absolute;
	left: 35px;
}
#menu label:before {
	font-size: 1.6em;
	content: "\2261";
	margin-left: 20px;
}
#menu ul.sub-menus {
	height: auto;
	overflow: hidden;
	width: 170px;
	position: absolute;
	z-index: 99;
	display: none;
}
#menu ul.sub-menus li {
	display: block;
	width: 100%;
}
#menu ul.sub-menus a {
	color: #414249;
	font-size: 14px;
	font-weight: 400;
}
#menu li:hover ul.sub-menus {
	display: block;
	border: 1px solid #e2e2e2;
	background: #ffffff;
}
#menu ul.sub-menus a:hover {
	color: #ff8f8f;
}

@media screen and (max-width: 800px) {
	#menu {
		position: relative;
	}
	#menu ul {
		background: #111;
		position: absolute;
		top: 100%;
		right: 0;
		left: 0;
		z-index: 3;
		height: auto;
		display: none;
	}
	#menu ul.sub-menus {
		width: 100%;
		position: static;
	}
	#menu ul.sub-menus a {
		padding-left: 30px;
	}
	#menu li {
		display: block;
		float: none;
		width: auto;
	}
	#menu input,
	#menu label {
		position: absolute;
		top: 0;
		left: 0;
		display: block;
	}
	#menu input {
		z-index: 4;
	}
	#menu input:checked + label {
		color: white;
	}
	#menu input:checked + label:before {
		content: "\00d7";
	}
	#menu input:checked ~ ul {
		display: block;
	}
}
