@font-face
	font-family: "svgfont"
	src: url('fonts/svgfont.eot')
	src: url('fonts/svgfont.eot?#iefix') format('eot'), url('fonts/svgfont.woff') format('woff'), url('fonts/svgfont.woff2') format('woff2'), url('fonts/svgfont.ttf') format('truetype')
	font-weight: normal
	font-style: normal

$icon-close: "\EA01"
$icon-car: "\EA02"
$icon-germany: "\EA03"
$icon-guarantee: "\EA04"
$icon-hand-made: "\EA05"
$icon-heart: "\EA06"
$icon-replacement: "\EA07"
$icon-twitter: "\EA08"
$icon-facebook: "\EA09"
$icon-pinterest: "\EA0A"
$icon-bg: "\EA0C"
$icon-search: "\EA0D"
$icon-heart-2: "\EA0E"
$icon-basket: "\EA0F"
$icon-person: "\EA10"
$icon-phone: "\EA11"
$icon-youtube: "\EA12"
$icon-insta: "\EA13"


%icon
	font-family: "svgfont"
	font-style: normal
	font-weight: normal
	text-rendering: auto
	speak: none
	line-height: 1
	-webkit-font-smoothing: antialiased
	-moz-osx-font-smoothing: grayscale

.icon, [class^="icon-"], [class*=" icon-"]
	@extend %icon
	display: inline-block

.icon-close:before
	content: $icon-close
.icon-car:before
	content: $icon-car
.icon-germany:before
	content: $icon-germany
.icon-guarantee:before
	content: $icon-guarantee
.icon-hand-made:before
	content: $icon-hand-made
.icon-heart:before
	content: $icon-heart
.icon-replacement:before
	content: $icon-replacement
.icon-twitter:before
	content: $icon-twitter
.icon-facebook:before
	content: $icon-facebook
.icon-pinterest:before
	content: $icon-pinterest
.icon-bg:before
	content: $icon-bg
.icon-search:before
	content: $icon-search
.icon-heart-2:before
	content: $icon-heart-2
.icon-basket:before
	content: $icon-basket
.icon-person:before
	content: $icon-person
.icon-phone:before
	content: $icon-phone
.icon-youtube:before
	content: $icon-youtube
.icon-insta:before
	content: $icon-insta