.footer {
	font-family: "Poppins";
	font-style: normal;
	background: #ff8f8f;
	.container {
		display: flex;
		justify-content: space-between;
		max-width: 1440px;
		margin: 0 auto;
		padding-left: 80px;
		padding-right: 80px;
		padding-top: 95px;
		.descript {
			width: 296px;
			h4 {
				font-weight: 600;
				font-size: 20px;
				line-height: 30px;
				color: #ffffff;
			}
			p {
				font-weight: 400;
				font-size: 14px;
				line-height: 21px;
				color: #ffffff;
				margin-top: 16px;
			}
		}
		.info {
			margin-left: -135px;
			ul {
				font-weight: 600;
				font-size: 20px;
				line-height: 30px;
				color: #ffffff;
				:first-child {
					margin-top: 8px;
				}
				li {
					a {
						font-weight: 400;
						font-size: 14px;
						line-height: 21px;
						color: #ffffff;
					}
				}
			}
		}
		.about {
			margin-left: -50px;

			ul {
				font-weight: 600;
				font-size: 20px;
				line-height: 30px;
				color: #ffffff;
				:first-child {
					margin-top: 8px;
				}
				li {
					a {
						font-weight: 400;
						font-size: 14px;
						line-height: 21px;
						color: #ffffff;
						margin-top: 8px;
					}
				}
			}
		}
		.newsletter {
			h4 {
				font-weight: 600;
				font-size: 20px;
				line-height: 30px;
				color: #ffffff;
				margin-bottom: 16px;
			}
			input {
				background: #ffffff;
				border-radius: 24px;
				border: none;
				width: 296px;
				height: 48px;
				font-weight: 400;
				font-size: 16px;
				line-height: 24px;
				color: #a8aab7;
			}
			button {
				display: block;
				background: #ffe4bb;
				width: 296px;
				height: 48px;
				border-radius: 38px;
				border: none;
				margin-top: 16px;
				font-weight: 600;
				font-size: 16px;
				line-height: 24px;
				text-align: center;
				letter-spacing: 0.04em;
				text-transform: uppercase;
				color: #e86e6e;
				margin-bottom: 16px;
				cursor: pointer;
				&:hover{
					background:#FFDCA7;
				}
			}
			span {
				a {
					font-weight: 400;
					font-size: 14px;
					line-height: 21px;
					color: #ffffff;
					margin-right: 24px;
				}
			}
			.b2b-link{
				margin-left: -2px;
			}
		}
	}

	hr {
		background: #f6adad;
		margin-top: 57px;
		margin-bottom: 34px;
		border: none;
		height: 1px;
	}

	.footer-down {
		display: flex;
		justify-content: space-between;
		max-width: 1440px;
		margin: 0 auto;
		padding-left: 80px;
		padding-right: 80px;
		.payment {
			margin-bottom: 42px;
			a {
				margin-right: 5px;
				img {
					width: 37px;
					height: 22px;
				}
			}
		}
		.copyright {
			font-weight: 400;
			font-size: 14px;
			line-height: 21px;
			color: #ffffff;
			margin-left: -165px;
		}
		.social {
			.icon-facebook, .icon-twitter, .icon-pinterest, .icon-insta, .icon-bg{
				color: #ffffff;
				font-size: 20px;
				width: 20px;
				margin-right: 20px;
			}
			.icon-youtube{
				color: #ffffff;
				font-size: 20px;
				width: 20px;
			}
			.icon-bg::before{
				width: 20px;
			}
		}
	}
}
